<template>
  <div id="navigation-bar">
    <v-navigation-drawer
      style="z-index: 1002"
      app
      hide-overlay
      fixed
      v-model="drawer"
    >
      <v-list nav>
        <v-list-item-group v-model="group" active-class="primary">
          <v-list-item
            exact
            v-for="link in links"
            :key="link.text"
            :to="{ name: link.route }"
            color="secondary"
          >
            <v-list-item-content>
              <v-list-item-title>{{ link.text }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar
      style="opacity: 0.8; z-index: 1001"
      elevation="5"
      color="black"
      app
      fixed
      scroll-threshold=""
    >
      <v-app-bar-nav-icon
        @click.stop="drawer = !drawer"
        class="hidden-md-and-up"
      ></v-app-bar-nav-icon>
      <v-container class="mx-auto py-0">
        <div class="d-flex align-center">
          <v-btn
            class="hidden-sm-and-down"
            v-for="link in links"
            :key="link.text"
            :to="{ name: link.route }"
            :ripple="link.isRipple"
            plain
            exact
            color="white"
          >
            <span class="ml-2">{{ link.text }}</span>
          </v-btn>
          <v-spacer ></v-spacer>
          <search-algolia></search-algolia>
          <v-dialog
            :overlay-opacity="0.95"
            v-if="!isAuthenticated"
            content-class="noBoxShadow"
            max-width="400"
            v-model="$store.state.navbar.showLoginSignup"
          >
            <login></login>
          </v-dialog>
          <v-btn v-if="!isAuthenticated" @click.stop="showDialog"
            >Đăng ký/Đăng nhập</v-btn
          >
          <v-menu v-if="isAuthenticated" offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn class="noTextTrans" tile color="primary" dark v-bind="attrs" v-on="on">
                <v-icon left>fas fa-user</v-icon>
                {{ $store.getters["user/getUsername"] }}
              </v-btn>
            </template>
            <v-list>
              <v-list-item>
                <v-btn tile class="noTextTrans" plain @click="onSignout">Đăng xuất</v-btn>
              </v-list-item>
            </v-list>
          </v-menu>
        </div>
      </v-container>
    </v-app-bar>
  </div>
</template>

<script>
import login from "../userComponents/loginSignUp";
import { userService } from "../../services/user.service";
import SearchAlgolia from './searchAlgolia.vue';

export default {
  components: { login,SearchAlgolia },
  data: () => ({
    drawer: false,
    group: null,
    links: [
      { text: "Trang chủ", route: "Home", isRipple: false },
      { text: "Nước hoa", route: "nonePerfume", isRipple: false },
      { text: "Nhãn hiệu", route: "brandList", isRipple: false },
      { text: "Nhóm mùi hương", route: "scentList", isRipple: false },
     // { text: "Về chúng tôi", route: "About", isRipple: false },
    ],
  }),
  watch: {
    group() {
      this.drawer = false;
    },
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters["user/getAuthen"];
    },
  },
  methods: {
    onSignout() {
      userService.logout();
      this.$store.dispatch("navbar/setReloadViewKey");
    },
    showDialog() {
      this.$store.dispatch("navbar/loginOrSignup");
    },
  },
};
</script>

<style>
.noBoxShadow {
  box-shadow: none !important;
}
.noTextTrans {
  text-transform: none !important;
}
</style>
